<template>
  <div class="model-details">
    <el-row>
      <el-col :span="24">
        <div class="grid-content">
          <h3>模型详情</h3>
        </div>
      </el-col>
    </el-row>
    <el-form
      :label-position="labelPosition"
      label-width="100px"
      :model="formLabelAlign"
      class="form"
    >
      <el-form-item label="模型ID:">
        <span class="span">模型ID</span>
      </el-form-item>
      <el-form-item label="模型名称:">
        <span class="span">模型名称</span>
      </el-form-item>
      <el-form-item label="属性:">
        <span class="span">属性</span>
      </el-form-item>
      <el-form-item label="标签:">
        <span class="span">标签</span>
      </el-form-item>
      <el-form-item label="服务器路径:">
        <span class="span">服务器路径</span>
      </el-form-item>
      <el-form-item label="API链接:">
        <span class="span">模型ID</span>
      </el-form-item>
    </el-form>
  </div>
</template>


<script>
import { mapGetters, mapActions, mapState } from "vuex";
import _ from "lodash";
export default {
  watch: {
    // flowData(val) {
    // }
  },
  props: {
    // data: {
    //   type: Object,
    //   default: false
    // }
  },
  components: {},
  data: function() {
    return {
      labelPosition: "right",
      formLabelAlign: {
        name: "",
        region: "",
        type: ""
      }
    };
  },
  computed: {
    //...mapState([""])
  },
  mounted() {},
  beforeCreate() {},
  created() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed: function() {},
  methods: {
    //...mapActions([""]),
  }
};
</script>

<style lang="scss">
.model-details {
  position: absolute;
  width: 100%;
  padding: 10px;
  background: #ffffff;

  .form {
    padding: 50px 500px;

    .span {
      font-weight: 800;
    }
  }
}
</style>



